import React, { Component } from 'react'

export default class AI extends Component {
  render() {
    return (
      <div>
      <section className="site-section bg-light" id="ai-section">
        <div className="container">
          <div className="row mb-5">
          </div>
          <div className="row site-section" id="ai-section">
            <div className="col-12 text-center" data-aos="fade">
              <a name="featured" />
              <h2 className="section-title" style={{fontSize: '28px'}}>Providers</h2>
            </div>
            <div className="col-12 text-center" data-aos="fade-up" data-aos-delay>
            </div>
            <div className="col-lg-2 text-left" data-aos="fade">
            </div>
            <div className="col-md-8 text-center" data-aos="fade">
              <p>We don't need to tell you that better nutrition results in better health outcomes for patients. You're one of the providers or healthcare orgs that is paving the way for wellness care. Suggest the <b>athenaBento</b> program to any patient who would like easier access to healthy meals for themselves and their families.</p>
              <p><b>Sign up</b> to learn more or <b>Sign In</b> to track program progress or subsidies for your participation.</p> 
            </div>
            <div className="col-12 text-center" data-aos="fade">
                <img src="images/featured-sm.jpg" alt="Image" /> 
            </div>
          </div>
        </div></section>
      <section>
        <div className="row site-section" id="ai-section" >
        </div>
      </section>
    </div>
      );
    }
  };